<template>
   <h1 v-if="!pokemon">Espere por favor...</h1>

   <template v-else>
      <h1>Â¿Quien es este pokemon?</h1>

      <PokemonPictureVue 
         :pokemonId="pokemon.id" 
         :showPokemon="showPokemon" 
      />

      <PokemonOptionsVue 
         :pokemons="pokemonArr"
         @selection="checkAnswer"
       />

       <div v-if="showAnswer" >
         <h2 class="fade-in">{{ message }}</h2>
         <button @click="newGame">
            Nuevo juego
         </button>
       </div>


   </template>

</template>

<script>
import PokemonOptionsVue from '@/components/PokemonOptions.vue'
import PokemonPictureVue from '@/components/PokemonPicture.vue'

import getPokemonOptions from '@/helpers/getPokemonOptions'

export default {
   components: { PokemonOptionsVue, PokemonPictureVue},
   data() {
      return{
         pokemonArr: [],
         pokemon: null,
         showPokemon: false,
         showAnswer: false,
         message: ''
      }
   },
   methods: {
      async mixPokemonArray() {
         this.pokemonArr = await getPokemonOptions()

         const rndInt = Math.floor( Math.random() * 4 )
         this.pokemon = this.pokemonArr[ rndInt ]
      },
      checkAnswer( selectedId ) {
         this.showPokemon = true
         this.showAnswer = true

         if( selectedId === this.pokemon.id ) {
            this.message = `Correcto, ${ this.pokemon.name }`
         }else {
            this.message = `Opps, era ${ this.pokemon.name }`

         }
      },
      newGame() {
         this.showPokemon  = false
         this.showAnswer   = false
         this.pokemonArr   = []
         this.pokemon      = null
         this.mixPokemonArray()
      }

      
   },
   mounted() {
      this.mixPokemonArray()
   }

}
</script>